<template>

    <v-card
    class="mx-auto rounded-10" 
    max-width="400"
    elevation="5"
    >
        <v-dialog
            max-width="1000"
            v-model="imgDialog"
            overlay-opacity="0.9"
            overlay-color="black"
        >
            <v-row class="ma-0 pa-0" justify="end">
                <v-btn icon x-large @click="imgDialog=false" dark>
                    <v-icon>
                        clear
                    </v-icon>
                </v-btn>
            </v-row>
            <v-carousel
                class="pt-5 hola"
            >
                <v-carousel-item
                v-for="(screen, i) in app.screens"
                :key="i"
                
                >
                    <div align="center">
                        <v-img :src="screen" class="hola" contain>
                            <template v-slot:placeholder>
                                <v-row
                                class="fill-height ma-0"
                                align="center"
                                justify="center"
                                >
                                <v-progress-circular indeterminate color="white"></v-progress-circular>
                                </v-row>
                            </template>
                        </v-img>
                    </div>
                </v-carousel-item>
            </v-carousel>
        </v-dialog>
        <v-carousel
            class="pt-5"
            cycle
            height="200"
            show-arrows-on-hover
            hide-delimiters
            progress
            :progress-color="$accentColor"
        >
            <v-carousel-item
            v-for="(screen, i) in app.screens"
            :key="i"
            >
                <div align="center">
                    <v-img :src="screen" class="carousel-imgs" @click="imgClicked(screen)" contain>
                        <template v-slot:placeholder>
                            <v-row
                            class="fill-height ma-0"
                            align="center"
                            justify="center"
                            >
                            <v-progress-circular indeterminate></v-progress-circular>
                            </v-row>
                        </template>
                    </v-img>
                </div>
            </v-carousel-item>
        </v-carousel>
        <v-card-title>{{app.softwareName}}</v-card-title>
        <v-card-text>
            {{app.description}}
            <div v-if="app.externalURL!=null">
                <a :href="app.externalURL" target="Starfall">{{app.hyperlinkText}}</a>
            </div>
        </v-card-text>
        <v-menu
            :close-on-content-click="false"
            :nudge-width="200"
            max-width="250px"
            absolute
            v-model="developmentInfoPopup"
            >
            <template v-slot:activator="{ on }">
                <v-row class="pa-0 ma-0" justify="center">
                    <v-btn 
                    small 
                    :color="$accentColor"
                    v-on="on"
                    light>
                    Ver info. de desarrollo
                    </v-btn>
                </v-row>
            </template>

            <v-card :color="$accentColor" light>
                <v-row class="pa-0 ma-0">
                        <v-col class="pa-0 ma-0" cols="10">
                            <v-card-title class="pb-0 pr-0">
                                {{app.softwareName}}
                            </v-card-title>
                        </v-col>
                        <v-col class="pa-0 ma-0" cols="2">
                            <v-btn icon large @click="developmentInfoPopup=false">
                                <v-icon>
                                    clear
                                </v-icon>
                            </v-btn>
                        </v-col>
                    </v-row>

                <v-card-text class="pb-0 pt-0">
                    <v-row class="pa-0 ma-0">
                        <v-col class="pa-0">
                            <p class="title ma-0">Front-end</p>
                        </v-col>
                        <v-col class="pa-0">
                            <p class="title ma-0">Back-end</p>
                        </v-col>
                    </v-row>
                </v-card-text>
                <v-row class="pa-0 ma-0">
                    <v-col cols="6" class="pa-0 ma-0">
                        <v-list-item>
                            <v-list-item-content>
                                <v-list-item-title>Framework</v-list-item-title>
                                <v-list-item-subtitle>{{app.frontendFramework}}</v-list-item-subtitle>
                            </v-list-item-content>
                        </v-list-item>
                        <v-list-item>
                            <v-list-item-content>
                                <v-list-item-title>Lenguaje</v-list-item-title>
                                <v-list-item-subtitle>{{app.frontendLanguage==null?'Ninguno':app.frontendLanguage}}</v-list-item-subtitle>
                            </v-list-item-content>
                        </v-list-item>
                    </v-col>
                    <v-col cols="6" class="pa-0 ma-0">
                        <v-list-item>
                            <v-list-item-content>
                                <v-list-item-title>Framework</v-list-item-title>
                                <v-list-item-subtitle>{{app.backendFramework==null?'Ninguno':app.backendFramework}}</v-list-item-subtitle>
                            </v-list-item-content>
                        </v-list-item>
                        <v-list-item>
                            <v-list-item-content>
                                <v-list-item-title>Lenguaje</v-list-item-title>
                                <v-list-item-subtitle>{{app.backendLanguage==null?'Ninguno':app.backendLanguage}}</v-list-item-subtitle>
                            </v-list-item-content>
                        </v-list-item>
                        <v-list-item>
                            <v-list-item-content>
                                <v-list-item-title>Database</v-list-item-title>
                                <v-list-item-subtitle>{{app.backendDB==null?'Ninguno':app.backendDB}}</v-list-item-subtitle>
                            </v-list-item-content>
                        </v-list-item>
                    </v-col>
                </v-row>
            </v-card>
        </v-menu>
        <v-row class="pa-0 ma-0">
            <v-list-item>
                <v-list-item-content>
                    <v-list-item-title class="subtitle-1 font-weight-bold"></v-list-item-title>
                </v-list-item-content>
            </v-list-item>
        </v-row>
    </v-card>
</template>
<script>
export default {
    props:{
        app:Object
    },
    data:()=>({
        selectedImg:String,
        imgDialog:false,
        developmentInfoPopup:false
    }),
    methods:{
        imgClicked(imageURL){
            this.selectedImg=imageURL;
            this.imgDialog=true;
        }
    }
}
</script>
<style scoped>
    .carousel-imgs {
        height: 150px;
    }
    .hola{
        height: 50vh;
        object-fit: contain
    }
</style>